<template>
  <div class="w_h100 index_page_box size0">
    <div class="bottom_box">
      <div class="lottery_btn size20 mb20" @click="go_page('/lottery')">点击抽奖</div>
      <div class="active_info" @click="go_page('/rule')">
        <span class="size12">活动规则</span>
        <img class="w10 mt5" src="@/assets/images/index/idx_down_arrow.png">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    go_page(url) {
      this.$router.push(url);
    }
  }
};
</script>
<style scoped>
.index_page_box {
  background-image: url("../assets/images/index/idx_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.bottom_box {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.lottery_btn {
  color: #fff;
  background: #ff8401;
  padding: 6px 40px;
}
.active_info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}
</style>


